@charset "UTF-8";
@use "sass:math";
@import "mixins";
@import "core";

$board-pos-top: 170px; //360px;
$bg-color: #C90D0F;
$bg-grandient: radial-gradient(circle, rgba(201, 13, 15, 1) 20%, rgba(140, 0, 4, 1) 90%);

body {
    background: url(../images/bg2.png) no-repeat center bottom;
    background-size: cover;
}

// loading
// =================
.loading {
    background: $bg-color;
    background: $bg-grandient;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99999;

    .loader {
        --w: 10ch;
        font-weight: bold;
        font-family: monospace;
        font-size: 30px;
        line-height: 1.2em;
        letter-spacing: var(--w);
        width: var(--w);
        overflow: hidden;
        white-space: nowrap;
        color: #fff;
        animation: l17 2s infinite;
    }

    .loader:before {
        content: "Loading...";
    }

    @keyframes l17 {
        0% {
            text-shadow:
                calc(0*var(--w)) -1.2em #fff, calc(-1*var(--w)) -1.2em #fff, calc(-2*var(--w)) -1.2em #fff, calc(-3*var(--w)) -1.2em #fff, calc(-4*var(--w)) -1.2em #fff,
                calc(-5*var(--w)) -1.2em #fff, calc(-6*var(--w)) -1.2em #fff, calc(-7*var(--w)) -1.2em #fff, calc(-8*var(--w)) -1.2em #fff, calc(-9*var(--w)) -1.2em #fff
        }

        4% {
            text-shadow:
                calc(0*var(--w)) 0 #fff, calc(-1*var(--w)) -1.2em #fff, calc(-2*var(--w)) -1.2em #fff, calc(-3*var(--w)) -1.2em #fff, calc(-4*var(--w)) -1.2em #fff,
                calc(-5*var(--w)) -1.2em #fff, calc(-6*var(--w)) -1.2em #fff, calc(-7*var(--w)) -1.2em #fff, calc(-8*var(--w)) -1.2em #fff, calc(-9*var(--w)) -1.2em #fff
        }

        8% {
            text-shadow:
                calc(0*var(--w)) 0 #fff, calc(-1*var(--w)) 0 #fff, calc(-2*var(--w)) -1.2em #fff, calc(-3*var(--w)) -1.2em #fff, calc(-4*var(--w)) -1.2em #fff,
                calc(-5*var(--w)) -1.2em #fff, calc(-6*var(--w)) -1.2em #fff, calc(-7*var(--w)) -1.2em #fff, calc(-8*var(--w)) -1.2em #fff, calc(-9*var(--w)) -1.2em #fff
