// =================
.section {
    // background: $bg-color;
    // background: $bg-grandient;
    display: block;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.arena {
    width: 1040px;
    height: 620px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -40%);
    transform-origin: center center;
    z-index: 999;

    @media screen and (max-width: 1600px) {
        transform: translate(-50%, -45%) scale(calc((100 * var(--vw)) / 1600));
    }

    @media screen and (max-height: 900px) {
        transform: translate(-50%, -45%) scale(calc(min((100 * var(--vw)) / 1600, (100 * var(--vh)) / 900)));
    }
}

.jSlots-wrapper {
    overflow: hidden;
    display: block;
}

.bg-item {
    position: absolute;
    z-index: 0;
    display: block;

    img {
        display: block;
    }

    &.title {
        top: 20px;
        left: 50%;
        margin-left: -450px;
        width: 900px;
        height: auto;
        z-index: 99;
    }

    &.board-single {
        width: 847px;
        height: auto;
        top: $board-pos-top;
        @include center-transform-horizontal;
    }

    &.building {
        left: math.div(-50, 1920) * 100vw;
        bottom: math.div(-30, 1920) * 100vw;
        width: math.div(680, 1920) * 100vw;
        height: auto;
        @include no-select;
        z-index: 3;
    }
}

.slot-arena {
    // width: 1920px;
    // height: 1080px;
    @include center-transform-horizontal;
    top: 100%;
    bottom: 0;
    z-index: 12;

    &.is-show {
        top: 0;
    }
}

.slot-wrap {
    color: #fff;
    font-family: 'Yeseva One', serif;
    font-weight: 400;
    font-size: 288px;
    line-height: 1;
    letter-spacing: .1em;
    text-indent: .1em;
    text-align: center;
    text-shadow: 0 8px 10px rgba(black, .3);
    width: 100%;
    position: relative;
    overflow: hidden;

    /* Added to fix scroll overflow */
    .name {
        margin-bottom: .25em;
        text-align: center;
    }

    .dept {
        font-size: .7em;
        text-align: center;
        font-weight: 500;
    }
}

.slot {
    display: block;
    margin: 0;
    padding: 0;
    list-style: none;

    .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
}

.slot-result {
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    text-shadow: 0 8px 10px rgba(black, .3);

}

.empty {
    // background: url(../images/empty.png) no-repeat center center;
    // background-size: contain;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
}

.btn-go {
    background: url(../images/btn_go.png) no-repeat center center;
    background-size: contain;
    cursor: pointer;
    display: block;
    overflow: hidden;
    outline: none;
    border-width: 0;
    position: absolute;
    left: 50%;
    z-index: 30;
    width: 187px;
    height: 187px;
    margin-left: -93px;
    transition: all .025s ease-in-out;

    span {
        position: absolute;
        top: 100%;
    }

    &:active {
        transform: scale(.9);
    }
}

.endtext {
    background: url(../images/cl.png) no-repeat center top;
    background-size: contain;
    position: absolute;
    width: 964px;
    height: 160px;
    top: 50%;
    left: 50%;
    z-index: 20;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    pointer-events: none;

    &.animate__animated {
        opacity: 1;
        pointer-events: auto;
    }
}

// ============================
// Single
// ============================
$slot-silgle__hight: 298px;

.single {
    width: 100%;

    .slot-wrap {
        width: 770px;
        height: $slot-silgle__hight;
        @include center-transform-horizontal;
        top: $board-pos-top + 32px;
    }

    .jSlots-wrapper {
        height: $slot-silgle__hight;
    }

    .slot-result {
        height: $slot-silgle__hight;
    }

    .slot {
        height: $slot-silgle__hight;

        .item {
            height: $slot-silgle__hight;
        }
    }

    // .empty{
    //     transform: translateY(12px);
    // }
}

.arena-single {
    .btn-go {
        top: $board-pos-top + 285px;
    }

    .endtext {
        top: $board-pos-top + 330px;
        width: 840px;
        height: 140px;
        margin-left: -420px;
    }
}

// control buttons
// ======================
.btn-go.disabled {
    pointer-events: none !important;
}

