    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1010;

    .col {
        position: absolute;
        bottom: 20px;
        display: flex;
        gap: 8px;
    }

    .col-left {
        left: 20px;
    }

    .col-right {
        right: 20px;
    }

    .btn {
        background-color: transparent;
        background: rgba(black, .5);
        //border: 0 solid #e5e7eb;
        border-radius: .25rem;
        box-sizing: border-box;
        color: #fff;
        column-gap: 1rem;
        display: block;
        font-size: 13px;
        font-weight: 400;
        line-height: 1;
        margin: 6px 0 0 0;
        opacity: .5;
        outline: 2px solid transparent;
        padding: 10px 8px;
        text-align: center;
        text-decoration: none;
        text-transform: none;
        transition: all .1s cubic-bezier(.4, 0, .2, 1);
