#replay {
    position: fixed;
    top: 0;
    left: 0;
    display: none;

    &.is-show {
        display: block;
        width: 100vw;
        height: 100vw;
        z-index: 1000;
    }
}

// fireworks
// ======================
#fireworks {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 2;
}

// stars
// ======================
.stars {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    width: 100vw;
    height: 70vh;
    z-index: 0;
    @include no-select;
    pointer-events: none;

    .star {
        position: absolute;
        background: white;
        // border-radius: 50%;
        opacity: 0;
        // box-shadow: 0 0 10px white, 0 0 2px white;
        filter: drop-shadow(0 0 2px rgba(255, 255, 255, 0.8));
        clip-path: polygon(50% 0%, 60% 40%, 100% 50%, 60% 60%, 50% 100%, 40% 60%, 0% 50%, 40% 40%);
        animation: twinkle 3s infinite ease-in-out;
    }
}

@keyframes twinkle {
    0% {
        opacity: 0;
        transform: scale(0.5);
    }

    50% {
        opacity: var(--opacity, 1);
        transform: scale(1.2);
    }

    100% {
        opacity: 0;
        transform: scale(0.5);
    }
}

// History Modal
// ======================
.modal {
    display: none;
    position: fixed;
    z-index: 99999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.6);
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(5px);

    &.is-open {
        display: flex;
    }

    .modal-content {
        background-color: #fff; // White background as requested
        margin: auto;
        padding: 56px 30px 30px;
        // border: 1px solid #888;
        width: 90%;
        max-width: 800px; // Wider for grid
        border-radius: 16px;
        position: relative;
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
        color: #333;
        text-align: left;
        animation: modalFadeIn 0.3s;
        display: flex;
        flex-direction: column;
        max-height: 80vh;
    }

    .modal-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        padding-bottom: 10px;
        border-bottom: 1px solid #eee;

        .title-grounp {
            display: flex;
            align-items: center;
            gap: 10px;

            // Icon placeholder style if needed, or use font icon
            .icon-file {
                // background: url(...)
                display: none; // hidden for now unless user provides icon
            }
        }

        h2 {
            margin: 0;
            color: #333;
            font-size: 1.5rem;
            font-weight: bold;
        }
    }

    .close-btn {
        position: absolute;
        top: 10px;
        right: 15px;
        font-size: 28px;
        color: #aaa;
        cursor: pointer;
        z-index: 10;
        display: block;
        line-height: 1;
        transition: color 0.2s;

        &:hover {
            color: #333;
        }
    }

    .history-list-wrap {
        overflow-y: auto;
        flex: 1;
        padding: 10px;
        background: transparent;

        /* Custom Scrollbar for webkit */
        &::-webkit-scrollbar {
            width: 8px;
        }

        &::-webkit-scrollbar-track {
            background: #f1f1f1;
            border-radius: 4px;
        }

        &::-webkit-scrollbar-thumb {
            background: #ccc;
            border-radius: 4px;
        }
    }

    .history-list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(60px, 1fr));
        gap: 15px;
        justify-items: center;

        li {
            width: 60px;
            height: 60px;
            background-color: #d50b1c; // RED as requested
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #fff;
            font-size: 1.2rem;
            font-weight: bold;
            box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
            transition: transform 0.2s;
            cursor: pointer;

            &:hover {
                transform: scale(1.1);
            }

            // Highlight specific one? (Like 'Current')
            &.latest {
                border: 3px solid #f2c94c; // Yellow border for latest?
            }
        }

        .empty-history {
            pointer-events: none;
            grid-column: 1 / -1;
            width: 100%;
            height: auto;
            background: transparent;
            color: #999;
            box-shadow: none;
            border-radius: 0;
        }
    }

    .btn-clear {
        background-color: transparent;
        color: #d50b1c;
        border: 1px solid #d50b1c;
        padding: 6px 16px;
        border-radius: 4px;
        font-size: 0.9rem;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
            background-color: #d50b1c;
            color: #fff;
        }
    }
}

@keyframes modalFadeIn {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}