* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}

body,
html {
  font-optical-sizing: auto;
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  font-size: 100%;
}

body {
  font-family: "Noto Sans TC", "PingFang TC", Roboto, "Segoe UI", SegoeUI, Helvetica, Arial;
  background-size: 100% auto;
  overflow: hidden;
  height: 100vw;
}

body {
  background: url(../images/bg2.png) no-repeat center bottom;
  background-size: cover;
}

.loading {
  background: #C90D0F;
  background: radial-gradient(circle, rgb(201, 13, 15) 20%, rgb(140, 0, 4) 90%);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99999;
}
.loading .loader {
  --w: 10ch;
  font-weight: bold;
  font-family: monospace;
  font-size: 30px;
  line-height: 1.2em;
  letter-spacing: var(--w);
  width: var(--w);
  overflow: hidden;
  white-space: nowrap;
  color: #fff;
  animation: l17 2s infinite;
}
.loading .loader:before {
  content: "Loading...";
}
