.section {
  display: block;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.arena {
  width: 1040px;
  height: 620px;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -40%);
  transform-origin: center center;
  z-index: 999;
}
@media screen and (max-width: 1600px) {
  .arena {
    transform: translate(-50%, -45%) scale(calc(100 * var(--vw) / 1600));
  }
}
@media screen and (max-height: 900px) {
  .arena {
    transform: translate(-50%, -45%) scale(min(100 * var(--vw) / 1600, 100 * var(--vh) / 900));
  }
}

.jSlots-wrapper {
  overflow: hidden;
  display: block;
}

.bg-item {
  position: absolute;
  z-index: 0;
  display: block;
}
.bg-item img {
  display: block;
}
.bg-item.title {
  top: 20px;
  left: 50%;
  margin-left: -450px;
  width: 900px;
  height: auto;
  z-index: 99;
}
.bg-item.board-single {
  width: 847px;
  height: auto;
  top: 170px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.bg-item.building {
  left: -2.6041666667vw;
  bottom: -1.5625vw;
  width: 35.4166666667vw;
  height: auto;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  z-index: 3;
}

.slot-arena {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 100%;
  bottom: 0;
  z-index: 12;
}
.slot-arena.is-show {
  top: 0;
}

.slot-wrap {
  color: #fff;
  font-family: "Yeseva One", serif;
  font-weight: 400;
  font-size: 288px;
  line-height: 1;
  letter-spacing: 0.1em;
  text-indent: 0.1em;
  text-align: center;
  text-shadow: 0 8px 10px rgba(0, 0, 0, 0.3);
  width: 100%;
  position: relative;
  overflow: hidden;
  /* Added to fix scroll overflow */
}
.slot-wrap .name {
  margin-bottom: 0.25em;
  text-align: center;
}
.slot-wrap .dept {
  font-size: 0.7em;
  text-align: center;
  font-weight: 500;
}

.slot {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
}
.slot .item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.slot-result {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  text-shadow: 0 8px 10px rgba(0, 0, 0, 0.3);
}

.empty {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

.btn-go {
  background: url(../images/btn_go.png) no-repeat center center;
  background-size: contain;
  cursor: pointer;
  display: block;
  overflow: hidden;
  outline: none;
  border-width: 0;
  position: absolute;
  left: 50%;
  z-index: 30;
  width: 187px;
  height: 187px;
  margin-left: -93px;
  transition: all 0.025s ease-in-out;
}
.btn-go span {
  position: absolute;
  top: 100%;
}
.btn-go:active {
  transform: scale(0.9);
}

.endtext {
  background: url(../images/cl.png) no-repeat center top;
  background-size: contain;
  position: absolute;
  width: 964px;
  height: 160px;
  top: 50%;
  left: 50%;
  z-index: 20;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  pointer-events: none;
}
.endtext.animate__animated {
  opacity: 1;
  pointer-events: auto;
}

.single {
  width: 100%;
}
.single .slot-wrap {
  width: 770px;
  height: 298px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 202px;
}
.single .jSlots-wrapper {
  height: 298px;
}
.single .slot-result {
  height: 298px;
}
.single .slot {
  height: 298px;
}
.single .slot .item {
  height: 298px;
}

.arena-single .btn-go {
  top: 455px;
}
.arena-single .endtext {
  top: 500px;
  width: 840px;
  height: 140px;
  margin-left: -420px;
}

.btn-go.disabled {
  pointer-events: none !important;
}

.ctrls {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1010;
}
.ctrls .col {
  position: absolute;
  bottom: 20px;
  display: flex;
  gap: 8px;
}
.ctrls .col-left {
  left: 20px;
}
.ctrls .col-right {
  right: 20px;
}
.ctrls .btn {
  background-color: transparent;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  box-sizing: border-box;
  color: #fff;
  column-gap: 1rem;
  display: block;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  margin: 6px 0 0 0;
  opacity: 0.5;
  outline: 2px solid transparent;
  padding: 10px 8px;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  transition: all 0.1s cubic-bezier(0.4, 0, 0.2, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  box-shadow: -6px 8px 10px rgba(81, 41, 10, 0.1), 0px 2px 2px rgba(81, 41, 10, 0.2);
}
.ctrls .btn .icon {
  display: inline-block;
  width: 24px;
  height: 24px;
}
.ctrls .btn:hover {
  opacity: 1;
}
.ctrls .btn:active {
  box-shadow: -1px 2px 5px rgba(81, 41, 10, 0.15), 0px 1px 1px rgba(81, 41, 10, 0.15);
  color: #482307;
  transform: translateY(0.125rem);
}
.ctrls .btn.btn-icon {
  padding: 0;
  height: 40px;
  width: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
